import React, {useState, useEffect} from 'react'
import {Modal, Button, Table, Tag} from 'antd'

import localStorage_change from '../../../#localStorage_change.js'
import Url from '../../../url/url.js'
import ReturnAge from './../return-age/returnAge.js' // Определяет возраст
import Pagination from './../#more-functions/pagination/pagination.js' // Pagination
import getLimit from './../#more-functions/getLimit/getLimit.js'
import RestrictionMessage from './../#more-functions/restriction-message/restriction-message.js'

import Subscriptions from './subscriptions/subscriptions.js'
// Библиотеки
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import moment from 'moment'

import {Link} from "react-router-dom"

import InputMask from 'react-input-mask'
// /Библиотеки

import styled from '@emotion/styled'
import './student.css'

const Student = ({setPageStudent}) => {

    const MySwalErr = withReactContent(Swal)
    const swallErr = (value) => {
        MySwalErr.fire({
            icon: 'error',
            title: 'Ой..',
            text: value
        })
    }

    const MySwaGood = withReactContent(Swal)
    const swallGood = () => {
        MySwaGood.fire({
            icon: 'success',
            title: 'Ученик создан'
        })
    }

    // Modal

    const [isModalVisible, setIsModalVisible] = useState(false)
    const [loading, setLoading] = useState(false)

    const showModal = () => {
        setIsModalVisible(true)
    }

    const handleCancel = () => {
        setIsModalVisible(false)
    }
    // /Modal

    // Filtered

    const [paramFilrers, setParamFilrers] = useState({
        surname: [],
        name: [],
        midname: [],
        age: [],
        gender: [],
        groups: [],
        balance: []// {
        // 	$gt: 0, // Больше
        // 	// $lte: 0 // Меньше или равно
        // 	$lt: 0 // Меньше
        // }
    })
    const [dataGroupsCheckedG, setDataGroupsCheckedG] = useState({})

    const [globalFilters, setGlobalFilters] = useState({
        surname: [],
        name: [],
        midname: [],
        age: []
    })

    const [loadingButtomFilrer, setLoadingButtomFilrer] = useState(false)

    const [filterDropdownVisible, setFilterDropdownVisible] = useState({
        surname: false,
        name: false,
        midname: false,
        age: false
    })
    // /Filtered

    const [offsetG, setOffsetG] = useState(0)
    const [Count, setCount] = useState(0)

    const axios = require('axios') // AJAX

    // Message
    const [message, setMessage] = useState(false)
    let borderInput = ''
    if (message) {
        borderInput = ' border-red'
    }
    // /Message

    const [dataInp, setDataInp] = useState({
        name: '',
        surname: '',
        midname: '',
        age: '',
        gender: '',
        phoneNumber: '',
        parentPhoneNumber: '',
        discordNickname: ''
    })

    // const [Token_i, setToken] = useState(Token)
    const [users, setUsers] = useState([
        {
            _id: '45343235456645',
            surname: "Список пуст...",
            name: "",
            midname: "",
            age: "2004-12-07",
            gender: "",
            phoneNumber: "",
            parentPhoneNumber: "",
            discordNickname: "",
            groups: []
        }
    ])

    const update = (offset = 0, param = {}, filtersGroups = {}) => {

        setLoadingButtomFilrer(true)

        let filtersLocal = {}

        for (let key in filtersGroups) {

            if (filtersLocal.groups) {
                filtersLocal.groups.push(key)
            } else {
                filtersLocal.groups = []
                filtersLocal.groups.push(key)
            }
            // filtersLocal.groups = [...filtersLocal.groups]
        }

        for (let key in param) {
            if (param[key][0]) {
                if (key == 'surname' || key == 'name' || key == 'midname' || key == 'age') {
                    filtersLocal[`${key}s`] = param[key]
                } else {
                    filtersLocal[`${key}`] = param[key]
                }
            }
        }

        if (filtersLocal.ages) {
            let age = [...filtersLocal.ages]
            filtersLocal.ages = []
            age.forEach(age => {
                filtersLocal.ages.push(+age)
            })
        }

        if (filtersLocal.balance) {
            let balance = [...filtersLocal.balance]
            if (balance[0] == 1 & !balance[1]) {
                filtersLocal.balance = {$gte: 0}
            } else if (balance[0] == 2 & !balance[1]) {
                filtersLocal.balance = {$lt: 0}
            } else if (balance[1]) {
                filtersLocal.balance = {$gte: 0, $lte: 0}
            }
        }

        let p = {
            filters: filtersLocal
        }

        axios({
            method: 'post',
            url: `${Url}/CRM/Pupils/find?limit=${getLimit}&offset=${offset}`, // ${10 + offset}
            headers: {
                'Content-Type': 'application/jsoncharset=utf-8',
                'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
            },
            data: p
        })
            .then((res) => {

                let {data, headers} = res
                setUsers(data)
                setCount(headers.count)

                setFilterDropdownVisible({
                    surname: false,
                    name: false,
                    midname: false
                })
                setLoadingButtomFilrer(false)
            })
            .catch((error) => {
                if (error.response) {
                    RestrictionMessage(error.response.status)
                    setLoadingButtomFilrer(false)
                    let {status, data} = error.response
                    if (status == 401) {

                        if (data.message == 'TOKEN_EXPIRED') {

                            localStorage_change(data.token)
                            update()
                        } else {

                            localStorage.removeItem('tokenID')
                            window.location.replace("/")
                        }
                    } else if (status == 500) {
                        swallErr('Ошибка сервера')
                    }
                }
            })
    }

    useEffect(() => {
        update()
    }, [])

    useEffect(() => {
        document.querySelectorAll("input").forEach((item) => {
            item.value = ''
        })
        document.querySelectorAll('select').forEach((item) => {
            item.value = ''
        })
    })

    const dataSource = []

    users.forEach((item) => {

        const trashUser = async () => {

            axios({
                method: 'DELETE',
                url: `${Url}/CRM/Pupils/${item._id}`,
                headers: {
                    'Content-Type': 'application/jsoncharset=utf-8',
                    'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
                }
            })
                .then((res) => {
                    update(offsetG)
                })
                .catch((error) => {
                    if (error.response) {
                        RestrictionMessage(error.response.status)
                        let {status, data} = error.response

                        if (data.message == 'TOKEN_EXPIRED') {
                            localStorage_change(data.token)
                            trashUser()

                        } else if (status == 401) {
                            localStorage.removeItem('tokenID')
                            window.location.replace("/")
                        }
                    }
                })
        }

        let {_id, surname, name, midname, age, gender, phone, balance, parentPhone, discord, groups} = item

        dataSource.push({
            key: _id,
            trash: trashUser,
            surname: () => (
                <Link
                    className="link-info"
                    onClick={() => {
                        setPageStudent(_id)
                    }}
                    to={`/student/${_id}`}>

                    {surname}
                </Link>
            ),
            name,
            midname,
            age: ReturnAge(age),
            gender,
            phoneNumber: phone,
            tutor: '',
            parentPhoneNumber: parentPhone,
            balance: `${balance}`,
            groups: groups? groups : [],
            discordNickname: discord
        })
    })

    let dataInput = {
        name: dataInp.name,
        surname: dataInp.surname,
        midname: dataInp.midname,
        age: dataInp.age,
        gender: dataInp.gender,
        phoneNumber: dataInp.phoneNumber,
        parentPhoneNumber: dataInp.parentPhoneNumber,
        discordNickname: dataInp.discordNickname
    }

    const formSubmit = async (e) => {

        setLoading(true)
        // setMessage('')

        let {
            name,
            surname,
            midname,
            login,
            password,
            gender,
            age,
            phoneNumber,
            parentPhoneNumber,
            discordNickname
        } = dataInput
        setDataInp(dataInput)

        e.preventDefault()
        // fetch(`${Url}/CRM/Pupils`, {
        // 	method: 'POST',
        // 	headers: {
        // 		'Content-Type': 'application/json;charset=utf-8',
        // 		'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
        // 	},
        // 	body: JSON.stringify({
        // 		name: name,
        // 		surname: surname,
        // 		midname: midname,
        // 		age: moment(age)._d.toISOString(),
        // 		gender: gender,
        // 		phone: phoneNumber,
        // 		parentPhone: parentPhoneNumber,
        // 		discord: discordNickname
        // 	})
        // })
        // .then((res) => {
        // 	setDataInp('')
        // 	setIsModalVisible(false)
        // 	setLoading(false)
        // 	swallGood()
        // 	update()
        // 	setMessage(false)
        // })

        axios({
            method: 'POST',
            url: `${Url}/CRM/Pupils`,
            headers: {
                'Content-Type': 'application/json;charset=utf-8',
                'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
            },
            data: {
                name: name,
                surname: surname,
                midname: midname,
                age: moment(age)._d.toISOString(),
                gender: gender,
                phone: phoneNumber,
                parentPhone: parentPhoneNumber,
                discord: discordNickname
            }
        })
            .then((res) => {
                setDataInp('')
                setIsModalVisible(false)
                setLoading(false)
                swallGood()
                update()
                setMessage(false)
            })
            .catch((error) => {
                if (error.response) {
                    RestrictionMessage(error.response.status)
                    setLoading(false)
                    let {status, data} = error.response

                    if (status == 400) {
                        swallErr('Не верно введены данные')
                        setMessage(true)
                    }
                    if (data.message == 'TOKEN_EXPIRED') {
                        localStorage_change(data.token)
                        formSubmit()
                    } else if (status == 401) {
                        localStorage.removeItem('tokenID')
                        window.location.replace("/")
                    } else if (status == 500) {
                        swallErr('Ошибка сервера')
                    }
                    document.querySelector('.nav-item').addEventListener('click', () => {
                        document.querySelectorAll("input").forEach((item) => {
                            item.value = ''
                        })
                    })
                }
            })
    }

    const breakpoints = [1020, 700],
        mq = breakpoints.map(
            bp => `@media (max-width: ${bp}px)`
        )

    const TableL = styled.div({
        padding: '0 30px',
        [mq[1]]: {
            padding: '0 0',
        },
        '.log_h2': {},
        td: {
            paddingTop: '1px',
            paddingBottom: '4px'
        },
        'th, td': {
            whiteSpace: 'nowrap'
        }
    })

    let formatNSM = {
        'А': '[А-Я]',
        'а': '[А-Яа-яA-Za-z]'
    }

    const getColumn = (title, dataIndex) => ({
        title: title,
        dataIndex: dataIndex,
    })

    const columns = [
        {
            title: "",
            dataIndex: "trash",
            key: "trash",
            render: (trash) => {
                if (trash) {
                    return (
                        <i
                            onClick={trash}
                            className="bi bi-trash"
                            style={{
                                fontSize: "30px",
                                color: "#F56767",
                                cursor: "pointer",
                            }}
                        />
                    );
                }
            },
        },
        {
            ...getColumn("Фамилия", "surname"),
            key: "surname",
            render: (surname) => {
                if (typeof surname == "function") {
                    return surname();
                }
            }
        },
        {
            ...getColumn("Имя", "name"),
            key: "name"
        },
        {
            ...getColumn("Отчество", "midname"),
            key: "midname"
        },
        {
            ...getColumn("Возраст", "age"),
            key: "age"
        },
        {
            ...getColumn("Пол", "gender"),
            key: "gender"
        },
        {
            ...getColumn(
                "Номер телефона",
                "phoneNumber"
            ),
            key: "phoneNumber"
        },
        {
            ...getColumn("Номер телефона родителя", "parentPhoneNumber"),
            key: "parentPhoneNumber",
        },
        {
            ...getColumn("Баланс", "balance"),
            key: "balance"
        },
        {
            ...getColumn("Группы", "groups"),
            key: "groups",
            render: (groups) => {
                if (typeof groups.map == "function") {
                    return (
                        <>
                            {groups.map((item) => (
                                <Tag
                                    color="blue"
                                    key={`${item._id}${item.GROUP_NAME}`}
                                >
                                    {item.GROUP_NAME}
                                </Tag>
                            ))}
                        </>
                    );
                }
            }
        },
        {
            ...getColumn("Дискорд никнейм", "discordNickname"),
            key: "discordNickname",
        },
    ]

    let inputMaskLet = (
        <InputMask
            onFocus={() => {
                document.querySelector("#inputSirname").value = ''
            }}
            mask="ааааааааааааааааааааааааа" maskChar={null} formatChars={formatNSM} autoComplete='off'
            onChange={(e) => {
                dataInput.surname = e.target.value
            }} required type="text" name="surname" className={`form-control${borderInput}`} id="inputSirname"/>
    )

    return (
        <>
            <nav id="navbar-student" className="navbar navbar-expand-lg navbar-light">
                <div className="container-fluid d-flex">
                    <div className="collapse d-flex flex-row h3-person">
                        <div className="d-flex plus-box">
                            <div>
                                <h3>Ученики</h3>
                            </div>
                        </div>
                    </div>
                    <div className="collapse d-flex flex-row-reverse" id="navbarScroll">
                        <div className="d-flex plus-box">
                            <div
                                className="nav-item plus"
                                onClick={showModal}
                            >
                                <i className="bi bi-person-plus" style={{fontSize: '38px', color: '#F56767'}}></i>
                            </div>
                        </div>
                    </div>
                </div>
            </nav>

            <TableL className=''>
                <div>
                    <div className={`table_user`}>
                        <Table
                            pagination={false}
                            dataSource={dataSource}
                            columns={columns}
                            scroll={{x: 0}}>
                        </Table>
                    </div>
                    <Pagination getItem={update} count={Count} offset={offsetG} setOffset={setOffsetG}/>
                </div>
            </TableL>
            <Modal
                title="Добавить ученика"
                visible={isModalVisible}
                onCancel={handleCancel}
                width={1000}
                footer={[
                    <Button key="onCancel1" onClick={handleCancel}>
                        Отмена
                    </Button>,
                    <Button
                        key="onOk1"
                        onClick={formSubmit}
                        loading={loading}
                        type="primary"
                    >
                        Сохранить
                    </Button>
                ]}
            >
                <form action="#" method="post">
                    <div className="modal-body">
                        <div className="row mb-3">
                            <label htmlFor="inputSirname" className="fs-5 col-sm-4 col-form-label">Фамилия</label>
                            <div className="col-sm-8">
                                {inputMaskLet}
                            </div>
                        </div>
                        <div className="row mb-3">
                            <label htmlFor="inputName" className="fs-5 col-sm-4 col-form-label">Имя</label>
                            <div className="col-sm-8">
                                <InputMask
                                    onFocus={() => {
                                        document.querySelector("#inputname").value = ''
                                    }}
                                    mask="ааааааааааааааааааааааааа" maskChar={null} formatChars={formatNSM}
                                    autoComplete='off' onChange={(e) => {
                                    dataInput.name = e.target.value
                                }} required type="text" name="name" className={`form-control${borderInput}`}
                                    id="inputname"/>
                            </div>
                        </div>
                        <div className="row mb-3">
                            <label htmlFor="inputMidname" className="fs-5 col-sm-4 col-form-label">Отчество</label>
                            <div className="col-sm-8">
                                <InputMask
                                    onFocus={() => {
                                        document.querySelector("#inputMidname").value = ''
                                    }}
                                    mask="ааааааааааааааааааааааааа" maskChar={null} formatChars={formatNSM}
                                    autoComplete='off' onChange={(e) => {
                                    dataInput.midname = e.target.value
                                }} required type="text" name="midname" className={`form-control${borderInput}`}
                                    id="inputMidname"/>
                            </div>
                        </div>
                        <div className="row mb-3">
                            <label htmlFor="inputName" className="fs-5 col-sm-4 col-form-label">Возраст</label>
                            <div className="col-sm-8">
                                <input autoComplete='off' onChange={(e) => {
                                    dataInput.age = e.target.value
                                }} required type="date" className={`form-control${borderInput}`} id="inputDate"/>
                            </div>
                        </div>
                        <div className="row mb-3">
                            <label htmlFor="inputMidname" className="fs-5 col-sm-4 col-form-label">Номер
                                Телефона</label>
                            <div className="col-sm-8">
                                <InputMask
                                    onFocus={() => {
                                        document.querySelector("#inputTel").value = ''
                                    }}
                                    autoComplete='off' onChange={(e) => {
                                    dataInput.phoneNumber = e.target.value
                                }} type="tel" name="tel" className={`form-control`} id="inputTel"
                                    mask="+9 (999) 999-99-99" placeholder='Не обязательно'/>
                            </div>
                        </div>
                        <div className="row mb-3">
                            <label htmlFor="inputName" className="fs-5 col-sm-4 col-form-label">Номер телефона
                                родителя</label>
                            <div className="col-sm-8">
                                <InputMask
                                    onFocus={() => {
                                        document.querySelector("#inputParent").value = ''
                                    }}
                                    autoComplete='off' onChange={(e) => {
                                    dataInput.parentPhoneNumber = e.target.value
                                }} type="tel" name="parentPhone" className={`form-control${borderInput}`}
                                    id="inputParent" mask="+9 (999) 999-99-99" required/>
                            </div>
                        </div>
                        <div className="row mb-3">
                            <label htmlFor="inputSirname" className="fs-5 col-sm-4 col-form-label">Дискорд
                                никнейм</label>
                            <div className="col-sm-8">
                                <input autoComplete='off' onChange={(e) => {
                                    dataInput.discordNickname = e.target.value
                                }} type="text" name="surname" className={`form-control`} id="inputDiscord"/>
                            </div>
                        </div>
                        <div className="row mb-3">
                            <label htmlFor="inputSirname" className="fs-5 col-sm-4 col-form-label">Пол</label>
                            <div className="col-sm-8 input-group">
                                <select defaultValue=' ' onChange={(e) => {
                                    dataInput.gender = e.target.value
                                }} required className="form-control" id="inputGroupSelect01">
                                    <option defaultValue></option>
                                    <option value="Мужской">Мужской</option>
                                    <option value="Женский">Женский</option>
                                </select>
                            </div>
                        </div>
                        <Subscriptions/>
                    </div>
                </form>
            </Modal>
        </>
    )
}

export default Student