import React, {useState} from 'react'
import styled from '@emotion/styled'

const SalesFunnel = () => {

    const [cardList, setCadtList] = useState([
        {id: 1, color: 'red', order: 4, text: 'КАРТОЧКА 4'},
        {id: 2, color: '#ff4', order: 3, text: 'КАРТОЧКА 3'},
        {id: 3, color: '#222', order: 2, text: 'КАРТОЧКА 2'},
        {id: 4, color: '#777', order: 1, text: 'КАРТОЧКА 1'}
    ])

    const [currentCard, setCurrentCard] = useState(null)

    const App = styled.div({
        width: '100vw',
        height: '100vh',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        '.card': {
            width: '200px',
            height: '300px',
            borderRadius: '12px',
            border: '5px solid',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            margin: '20px',
            cursor: 'grab'
        }
    })

    function dragStartHandler(e, card) {
        console.log('drag', card)
        setCurrentCard(card)
    }

    function dragEndHandler(e, color) {
        e.target.style.background = color
    }

    function dragOverHandler(e) {
        e.preventDefault()
        e.target.style.background = 'lightgray'
    }

    function dropHandler(e, card) {
        e.preventDefault()
        setCadtList(cardList.map(c => {
            if (c.id === card.id) {
                return {...c, order: currentCard.order}
            }
            if (c.id === currentCard.id) {
                return {...c, order: card.order}
            }
            return c
        }))
        e.target.style.background = '#fff'
    }

    const sortCards = (a, b) => {
        if (a.order > b.order) {
            return 1
        } else {
            return -1
        }
    }

    return (
        <App>
            {/*{cardList.sort(sortCards).map(card =>*/}
            {/*    <div*/}
            {/*        onDragStart={(e) => dragStartHandler(e, card)}*/}
            {/*        onDragLeave={(e) => dragEndHandler(e, card.color)}*/}
            {/*        onDragEnd={(e) => dragEndHandler(e, card.color)}*/}
            {/*        onDragOver={(e) => dragOverHandler(e)}*/}
            {/*        onDrop={(e) => dropHandler(e, card)}*/}
            {/*        draggable={true}*/}
            {/*        className={'card'}*/}
            {/*        style={{background: `${card.color}`}}>*/}
            {/*        {card.text}*/}
            {/*    </div>*/}
            {/*)}*/}
        </App>
    )
}

export default SalesFunnel